.Docs {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  gap: 0;
  min-height: 100vh
}

.Docs::-webkit-scrollbar {
  display: none; /* Hide scrollbar in WebKit browsers (Chrome, Safari, Edge) */
}

.docs-sig {
  width: 0px;
  height: 15px;
  position: relative;
  margin-left: 95px;
  image-rendering: pixelated;
}

.doc-content {
  flex: 1;
  padding-top: 2%;
  padding-left: 25%; 
  padding-right: 25%; 
  display: flex; 
  flex-direction: column; 
  justify-content: center; 
  box-sizing: border-box; 
  gap: 20px;
  margin-top: 55px;
}

.doc-header {
  text-align: left;
  height: auto;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  margin: 0;
  padding: 0; 
  line-height: 1; 
  color: white;
}

.doc-header-link,
.doc-paragraph {
  text-align: left;
  height: auto;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !important;
  margin: 0;
  padding: 0;
  line-height: 1.75;
  color: #c2c7d6 !important;
}

code.doc-paragraph {
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

a.doc-paragraph {
  color: #3f8aff !important;
  text-decoration: none;
}

a.doc-paragraph:hover {
  text-decoration: underline;
}

.doc-header-line {
  width: 100%;
  height: 2px;
  background-color: #333;
}

.doc-header,
.doc-paragraph,
.note-body,
.warn-body,
.important-body,
.filename,
.open-sans-font {
  font-family: "Open Sans", sans-serif !important;
  font-optical-sizing: auto !important;
  font-weight: 'Medium 500' !important;
  font-variation-settings:
    "wdth" 100;
}

.hljs {
  background: #1e1e1e !important;
  color: #dcdcdc !important;
}

.hljs,
.hljs-keyword,
.hljs-number,
.hljs-string,
.hljs-comment,
.hljs-title,
.hljs-params,
.hljs-built_in,
.hljs-function,
.hljs-literal,
.hljs-attr,
.hljs-selector-class,
.hljs-selector-id,
.hljs-selector-tag,
.hljs-template-tag,
.hljs-template-variable,
.hljs-type,
.hljs-variable {
  font-family: monospace;
}

.code-block-container {
  position: relative; 
  background-color: #1e1e1e; 
  border-radius: 6px; 
  margin-top: 24px;
}

.filename {
  position: absolute; 
  top: -20px; 
  left: 8px; 
  font-size: 12px;
  color: #888; 
  background-color: #1e1e1e; 
  padding: 2px 8px; 
  border-radius: 4px 4px 0 0; 
}

.code-block {
  margin: 0; 
  padding: 16px; 
  overflow-x: auto;
  white-space: pre;
  font-family: monospace; 
  background-color: #1e1e1e; 
  color: #f8f8f2; 
  border-radius: 4px; 
}

.copy-btn {
  position: absolute; 
  top: 8px; 
  right: 8px; 
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff; 
  border: none;
  padding: 5px 10px;
  font-size: 12px; 
  border-radius: 5px;
  cursor: pointer;
  transition: opacity 0.2s ease-in-out, background 0.2s;
  opacity: 0; 
}

.code-block-container:hover .copy-btn {
  opacity: 1;
}

.copy-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

.copy-btn.copied {
  background: #4caf50;
}

.image-thumbnail {
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  display: block;
  margin: 0 auto;
}

.image-thumbnail:hover {
  transform: scale(1.005);
}

.image-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: background 0.3s ease-in-out, opacity 0.3s ease-in-out;
  z-index: 1000;
}

.image-overlay.visible {
  background: rgba(0, 0, 0, 0.8);
  opacity: 1;
}

.image-container {
  position: relative;
  max-width: 90%;
  max-height: 90%;
  transform: scale(0.95);
  opacity: 0;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

.image-container.visible {
  transform: scale(1);
  opacity: 1;
}

.enlarged-image {
  max-width: 100%;
  max-height: 90vh;
  border-radius: 12px;
  box-shadow: 0px 4px 10px rgba(255, 255, 255, 0.2);
}

.close-button {
  position: absolute;
  top: -30px;
  right: -30px;
  background: rgba(0, 0, 0, 0.7); 
  color: white; 
  border: none; 
  font-size: 16px; 
  font-weight: bold; 
  cursor: pointer; 
  width: 32px; 
  height: 32px; 
  border-radius: 50%; 
  display: flex;
  align-items: center; 
  justify-content: center; 
  transition: background 0.2s, transform 0.2s; 
}

.close-button:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

