.API {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    gap: 0;
    min-height: 100vh
}

.API::-webkit-scrollbar {
    display: none;
    /* Hide scrollbar in WebKit browsers (Chrome, Safari, Edge) */
}

.api-sig {
    width: 0px;
    height: 15px;
    position: relative;
    margin-left: 95px;
    image-rendering: pixelated;
}

.api-content {
    flex: 1;
    padding-top: 2%;
    padding-left: 25%;
    padding-right: 25%;
    display: flex;
    flex-direction: column;
    justify-content: left;
    box-sizing: border-box;
    gap: 20px;
    margin-top: 55px;
}

.api-header {
    text-align: left;
    height: auto;
    font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    margin: 0;
    padding: 0;
    line-height: 1;
    color: white;
}

.api-header-line {
    width: 100%;
    height: 2px;
    background-color: #333;
}

.api-paragraph {
    text-align: left;
    height: auto;
    font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !important;
    margin: 0;
    padding: 0;
    line-height: 1.75;
    color: #c2c7d6 !important;
}

code.api-paragraph {
    font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

a.api-paragraph {
    color: #3f8aff;
    text-decoration: none;
}

a.api-paragraph:hover {
    text-decoration: underline;
}

.api-header,
.api-paragraph .open-sans-font {
    font-family: "Open Sans", sans-serif !important;
    font-optical-sizing: auto !important;
    font-weight: 'Medium 500' !important;
    font-variation-settings:
        "wdth" 100;
}


.api-name-text,
.api-name-header {
    text-align: left;
    height: auto;
    font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    margin: 0;
    padding: 0;
    line-height: 1.75;
    color: #c2c7d6 !important;
    font-size: larger;
    font-weight: bold;
}

.api-name-text {
    font-weight: normal;
    font-size: medium;
}

.highlighter-container {
    position: relative;
    display: inline-block;
    transition: all 0.3s ease-in-out;
}

.highlighter-container {
    position: relative;
    display: inline-block;
    transition: all 0.3s ease-in-out;
}

.output {
    color: white;
    font-size: larger;
    font-family: monospace;
    text-align: left;
    overflow-x: auto;
}


.keyword {
    color: #f97583;
}

.global {
    color: #79b8ff;
}

.comment {
    color: #6a737d;
    font-style: italic;
}